.App{
  width: 100vw;
  height: 100vh;
}
.contenedor{
  height: 100vh;
  box-shadow: 0px 0px 20px 0px #222;
  border-radius: 50px;
}
.contenido{
  height: 90%;
  overflow: auto;
  padding: 0px 10px;
}

@media  screen and (max-width: 500px) {
  .contenedor{
    border-radius: 0;
  }
}


.contenido {
  box-sizing: border-box;
}

/* Estilos para motores Webkit y blink (Chrome, Safari, Opera... )*/

.contenido::-webkit-scrollbar {
  -webkit-appearance: none;
}

.contenido::-webkit-scrollbar:vertical {
  width:10px;
}

.contenido::-webkit-scrollbar-button:increment,.contenedor::-webkit-scrollbar-button {
  display: none;
} 

.contenido::-webkit-scrollbar:horizontal {
  height: 10px;
}

.contenido::-webkit-scrollbar-thumb {
  background-color: #52833c;
  border-radius: 20px;
  border: 1px solid #f1f2f3;
}

.contenido::-webkit-scrollbar-track {
  border-radius: 10px;  
}